.clearfix {
  zoom: 1;
}
.clearfix:after,
.clearfix:before {
  display: table;
  content: '';
}
.clearfix:after {
  clear: both;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
html {
  height: 100%;
  font-size: 16px;
  line-height: 1;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: #000;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
textarea {
  resize: none;
}
input,
select,
button {
  outline: none;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
::selection {
  background: #b3d4fc;
  text-shadow: none;
}
body {
  position: relative;
  overflow-x: hidden;
  min-width: 320px;
  text-size-adjust: none;
  -webkit-font-smoothing: antialiased;
  font-family: "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
  font-size: 16px;
  line-height: 1;
  color: #000;
  height: 100%;
}
.wrapper {
  height: 100%;
}
.content {
  box-sizing: border-box;
  min-height: 100%;
  padding-bottom: 52px;
}
.footer {
  height: 313px;
  margin-top: 10px;
}
.container {
  position: relative;
  width: 98%;
  max-width: 1151px;
  margin: 0 auto;
}
.header__gamburger {
  position: relative;
  display: none;
  height: 26px;
  width: 30px;
  margin: 0 auto;
  cursor: pointer;
}
.header__gamburger:hover {
  opacity: 0.7;
}
.header__gamburger.active .header__gamburger-item--top {
  transform: rotate(45deg);
  top: 11px;
}
.header__gamburger.active .header__gamburger-item--middle {
  opacity: 0;
}
.header__gamburger.active .header__gamburger-item--bottom {
  transform: rotate(-45deg);
  top: 11px;
}
.header__gamburger-item {
  display: block;
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 3px;
  background-color: #1d275a;
}
.header__gamburger-item--top {
  transition: 0.3s linear;
  top: 0;
}
.header__gamburger-item--middle {
  transition: 0.3s linear;
  top: 11px;
}
.header__gamburger-item--bottom {
  transition: 0.3s linear;
  bottom: 0;
}
.header__top {
  padding: 16px 0;
  margin-bottom: 30px;
}
.header__top .container {
  zoom: 1;
}
.header__top .container:after,
.header__top .container:before {
  display: table;
  content: '';
}
.header__top .container:after {
  clear: both;
}
.header__logo {
  position: relative;
  float: left;
}
.header__logo a {
  display: block;
}
.header__logo img {
  display: block;
  position: absolute;
  left: -5px;
}
.header__contacts {
  float: right;
  margin-top: 64px;
}
.header__contacts-text {
  margin-right: 6px;
  font: 18px/18px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.header__contacts-phone {
  display: inline-block;
  margin: 5px 13px 0 0;
  font: 26px/26px "FuturaMedium", Arial, Tahoma, sans-serif;
  opacity: 1;
}
.header__contacts-phone:hover {
  opacity: 0.5;
}
.header__contacts-btn {
  display: inline-block;
  vertical-align: top;
  height: 32px;
  width: 150px;
  padding: 9px 11px 8px;
  margin-right: 2px;
  border: 1px solid transparent;
  border-radius: 3px;
  color: #fff;
  font: 14px/14px "FuturaLight", Arial, Tahoma, sans-serif;
  background-color: #1d275a;
  cursor: pointer;
  text-transform: uppercase;
  text-align: center;
  transition: border 0.3s linear, background-color 0.3s linear, color 0.3s linear;
}
.header__contacts-btn:hover {
  background-color: #fff;
  color: #1d275a;
  border: 1px solid #1d275a;
}
.header__contacts-btn:active {
  background-color: #fff;
  color: #1d275a;
  border-bottom: 6px solid #1d275a;
}
.header__bottom {
  position: relative;
  border-top: 1px solid #e2ddd8;
}
.header__bottom:before {
  content: '';
  position: absolute;
  top: 4px;
  height: 1px;
  width: 100%;
  background: #e2ddd8;
}
.header__nav {
  padding: 17px 0 9px;
  text-align: center;
}
.header__nav-list.active {
  display: block;
}
.header__nav-list li {
  position: relative;
  display: inline-block;
  margin-right: 2.44%;
}
.header__nav-list li:last-child {
  margin-right: 0;
}
.header__nav-list li:before {
  content: '';
  width: 0;
  height: 4px;
  transition: width 0.3s linear;
}
.header__nav-list li:hover:before {
  content: '';
  position: absolute;
  top: -17px;
  left: 0;
  background-color: #1c285b;
  height: 4px;
  width: 100%;
}
.header__nav-list li:hover .header__dropdown {
  left: -2px;
}
.header__nav-list a {
  display: block;
  padding: 11px 0 9px;
  font: 14px/14px "FuturaLight", Arial, Tahoma, sans-serif;
  color: #515151;
  text-transform: uppercase;
  transition: color 0.3s linear;
}
.header__nav-list a:hover {
  color: #1c285b;
}
.header__dropdown {
  position: absolute;
  left: -9999px;
  top: 43px;
  width: 199px;
  padding: 22px 0 26.4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.37);
  background-color: #1d275a;
  text-align: left;
}
.header__dropdown:before {
  content: '';
  position: absolute;
  top: -15px;
  left: 0;
  width: 100%;
  height: 32px;
  background-color: transparent;
}
.header__dropdown li {
  position: static;
  width: 100%;
  margin: 0 0 9px 0;
}
.header__dropdown li:before {
  content: none;
}
.header__dropdown li:hover:before {
  content: none;
}
.header__dropdown li:last-child {
  margin-bottom: 0;
}
.header__dropdown li:hover .dropdown__img {
  left: 100%;
}
.header__dropdown a {
  display: block;
  width: 100%;
  padding: 0 26px;
  color: #e2ddd8;
  font: 14px/14px "FuturaLight", Arial, Tahoma, sans-serif;
  transition: color 0.3s linear;
  text-transform: none;
}
.header__dropdown a:hover {
  color: #c4ab2e;
}
.header__dropdown .dropdown__img {
  border-left: 1px solid #fff;
  position: absolute;
  left: -9999px;
  top: 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.37);
}
.header__dropdown .dropdown__img img {
  display: block;
}
