.slick-prev {
  display: block;
  width: 45px;
  height: 84px;
  border-style: dashed;
  border-width: 42px 0 42px 44px;
  border-color: transparent transparent transparent #fff;
  left: 0;
  z-index: 2;
  transition: opacity 0.3s linear;
}
.slick-prev:before {
  z-index: 2;
  content: '';
  position: absolute;
  left: -48px;
  top: -16px;
  display: block;
  width: 14px;
  height: 36px;
  background: url('../img/pngSprite/sprite.png') -37px 0 no-repeat;
}
.slick-next {
  display: block;
  width: 45px;
  height: 84px;
  border-style: dashed;
  border-width: 42px 44px 42px 0;
  border-color: transparent #fff transparent transparent;
  right: -1px;
  z-index: 2;
  transition: opacity 0.3s linear;
}
.slick-next:before {
  z-index: 2;
  content: '';
  position: absolute;
  left: 33px;
  top: -16px;
  transform: rotate(180deg);
  display: block;
  width: 14px;
  height: 36px;
  background: url('../img/pngSprite/sprite.png') -37px 0 no-repeat;
}
.slick-slide {
  outline: none;
}
.slick-dots {
  width: auto;
  right: 0;
  bottom: -34px;
}
.slick-dots .slick-active {
  color: #1c285b;
}
.slick-dots li {
  margin: 0 -1px;
}
.slick-dots li button:before {
  color: #515151;
  font-size: 13px;
}
.top-banner {
  background: #515151 url("../img/general/top-banner-bg.jpg") 0 0 no-repeat;
  background-size: cover;
  min-height: 233px;
}
.wedding-item {
  margin-bottom: 57px;
}
.wedding-item__top {
  padding: 21px 0 15px;
  background-color: #1d275a;
}
.wedding-item__top .container {
  padding: 0 6px;
}
.wedding-item__title {
  font: 35px/35px "FuturaLight", Arial, Tahoma, sans-serif;
  color: #fff;
  text-transform: uppercase;
}
.wedding-item__bottom .container {
  zoom: 1;
}
.wedding-item__bottom .container:after,
.wedding-item__bottom .container:before {
  display: table;
  content: '';
}
.wedding-item__bottom .container:after {
  clear: both;
}
.wedding-item__left {
  width: 47%;
  float: left;
  padding: 60px 43px 20px 4px;
}
.wedding-item__left p {
  margin-bottom: 20px;
  color: #787878;
  font: 13px/20px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
  text-align: justify;
}
.wedding-item__left a {
  color: #1d275a;
  font: 13px/13px "LucidaGrandeBold", Arial, Tahoma, sans-serif;
}
.wedding-item__left a:hover {
  opacity: 0.5;
}
.wedding-item__right {
  float: right;
  position: relative;
  width: 53%;
}
.wedding-item__slider {
  margin-top: -135px;
  margin-right: -6px;
  border: 9px solid #fff;
  background-color: #fff;
}
.wedding-item__slider img {
  width: 673px;
  height: 548px;
}
.other-wedding {
  position: relative;
  padding: 26px 0 20px 0;
  border-top: 1px solid #e2ddd8;
  text-align: center;
}
.other-wedding:before {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  height: 1px;
  width: 100%;
  background: #e2ddd8;
}
.other-wedding__item {
  display: inline-block;
  max-width: 367px;
  width: 100%;
  margin-right: 17px;
  overflow: hidden;
}
.other-wedding__item:last-child {
  margin-right: 0;
}
.other-wedding__title {
  margin-bottom: 16px;
  font: 18px/18px "FuturaLight", Arial, Tahoma, sans-serif;
  color: #5a5a5a;
  text-transform: uppercase;
  text-align: center;
}
.other-wedding__img {
  position: relative;
  transition: all 0.3s linear;
}
.other-wedding__img img {
  display: block;
}
.other-wedding__img:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(28, 40, 91, 0);
  transition: all 0.3s linear;
}
.other-wedding__img:hover:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  background-color: rgba(28, 40, 91, 0.6);
  transition: all 0.3s linear;
}
.other-wedding__img:hover .other-wedding__hover {
  opacity: 1;
}
.other-wedding__hover {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: block;
  padding: 57px 27px 10px 36px;
  opacity: 0;
  transition: 0.3s linear;
}
.other-wedding__text {
  position: relative;
  padding-left: 40px;
  margin-bottom: 43px;
  text-align: justify;
  color: #fff;
  font: 13px/20px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.other-wedding__text:before {
  content: '';
  display: block;
  width: 22px;
  height: 80px;
  background: url('../img/pngSprite/sprite.png') 0 0 no-repeat;
  position: absolute;
  left: 0;
  top: -2px;
}
.other-wedding__btn {
  display: inline-block;
  padding: 3px 10px;
  border: 2px solid #fff;
  font: 14px/25px "FuturaMedium", Arial, Tahoma, sans-serif;
  color: #fff;
  text-transform: uppercase;
  transition: 0.3s linear;
}
.other-wedding__btn:hover {
  color: #000;
  background-color: #fff;
}
::placeholder {
  color: #959595;
}
.footer {
  position: relative;
  border-top: 1px solid #e2ddd8;
  text-align: center;
}
.footer:before {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  height: 1px;
  width: 100%;
  background: #e2ddd8;
}
.footer__social {
  z-index: 1;
  width: 155px;
  background-color: #fff;
  margin: -14px auto 43px;
}
.footer__social-item {
  display: inline-block;
  margin: 0 5px;
}
.footer__social-link {
  display: inline-block;
  vertical-align: middle;
}
.footer__social-link--fb {
  background: url('../img/pngSprite/sprite.png') -37px -51px no-repeat;
  width: 11px;
  height: 21px;
}
.footer__social-link--fb:hover {
  background: url('../img/pngSprite/sprite.png') -64px -95px no-repeat;
}
.footer__social-link--pr {
  background: url('../img/pngSprite/sprite.png') -31px -93px no-repeat;
  width: 17px;
  height: 21px;
}
.footer__social-link--pr:hover {
  background: url('../img/pngSprite/sprite.png') 1px -93px no-repeat;
}
.footer__social-link--tw {
  background: url('../img/pngSprite/sprite.png') -100px -28px no-repeat;
  width: 22px;
  height: 28px;
}
.footer__social-link--tw:hover {
  background: url('../img/pngSprite/sprite.png') -100px 5px no-repeat;
}
.footer__social-link--in {
  background: url('../img/pngSprite/sprite.png') -65px -34px no-repeat;
  width: 22px;
  height: 22px;
}
.footer__social-link--in:hover {
  background: url('../img/pngSprite/sprite.png') -65px 1px no-repeat;
}
.footer__bottom {
  padding-bottom: 48px;
}
.footer__bottom-item {
  text-align: left;
  display: inline-block;
  vertical-align: top;
  width: 33%;
}
.footer__bottom-item--middle {
  padding-left: 65px;
}
.footer__bottom-item--middle h2 {
  margin: 0 0 1px 0;
}
.footer__bottom-item--last {
  padding-left: 86px;
}
.footer__bottom-item--last h2 {
  margin: 0 0 2px 3px;
}
.footer__bottom-item p {
  margin: 0 0 6px 4px;
  color: #959595;
  font: 13px/20px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.footer__bottom-item a {
  display: block;
  color: #959595;
  font: 13px/25px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.footer__bottom-item a:hover {
  opacity: 0.7;
}
.footer__bottom-item a span {
  margin-left: 6px;
  font-family: "LucidaGrandeBold";
  color: #1c285b;
}
.footer__bottom-social {
  position: relative;
  padding-left: 26px;
}
.footer__bottom-social--phone:before {
  content: '';
  position: absolute;
  top: 6px;
  left: 7px;
  display: inline-block;
  background: url('../img/pngSprite/sprite.png') -113px -95px no-repeat;
  width: 5px;
  height: 16px;
}
.footer__bottom-social--phone:hover:before {
  opacity: 0.7;
}
.footer__bottom-social--mail:before {
  content: '';
  position: absolute;
  top: 4px;
  left: 2px;
  display: inline-block;
  background: url('../img/pngSprite/sprite.png') -101px -66px no-repeat;
  width: 14px;
  height: 14px;
}
.footer__bottom-social--mail:hover:before {
  opacity: 0.7;
}
.footer__bottom-title {
  margin: 0 0 4px 3px;
  color: #5a5a5a;
  font: 17px/25px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
  text-transform: uppercase;
}
.footer__bottom-list {
  display: inline-block;
  margin-right: 29px;
}
.footer__bottom-list:last-child {
  margin-right: 0;
}
.footer__bottom-list a {
  display: block;
  color: #959595;
  font: 13px/25px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
  transition: 0.3s linear;
}
.footer__bottom-list a:hover {
  color: #c4ab2f;
}
.footer__bottom-form-title {
  margin: 0 0 8px 4px;
  color: #959595;
  font: 13px/25px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.footer__bottom-form-wrap {
  position: relative;
  width: 291px;
  margin: 0 auto;
}
.footer__bottom-form input {
  margin-left: 4px;
  width: 288px;
  height: 40px;
  padding: 8px 42px 8px 13px;
  color: #000;
  font: 13px/22px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
  background-color: transparent;
  border: 1px solid #e2ddd8;
  transition: border 0.3s linear;
}
.footer__bottom-form input:focus {
  border: 1px solid #c4ab2f;
}
.footer__bottom-form-btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
  border-left: 1px solid #e2ddd8;
  transition: background-color 0.3s linear;
  cursor: pointer;
}
.footer__bottom-form-btn:hover {
  background-color: #c4ab2f;
}
.footer__bottom-form-btn:before {
  content: '';
  position: absolute;
  top: 13px;
  left: 14px;
  display: block;
  width: 8px;
  height: 16px;
  background: url('../img/pngSprite/sprite.png') -90px -95px no-repeat;
}
.footer__coppy {
  padding: 14px 0;
  text-align: left;
  background-color: #f4f3f3;
}
.footer__coppy .container {
  padding: 0 3px;
}
.footer__coppy-text {
  color: #959595;
  font: 11px/28px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.footer__coppy-left {
  display: inline-block;
}
.footer__coppy-right {
  float: right;
}
.footer__coppy-list {
  display: inline-block;
}
.footer__coppy-list li {
  display: inline-block;
  position: relative;
  margin-left: 12px;
}
.footer__coppy-list li:before {
  content: '';
  position: absolute;
  bottom: 8px;
  right: -10px;
  display: block;
  background-color: #959595;
  width: 2px;
  height: 10px;
}
.footer__coppy-list li:last-child:before {
  content: none;
}
.footer__coppy-list a {
  color: #959595;
  font: 11px/28px "LucidaGrandeRegular", Arial, Tahoma, sans-serif;
}
.footer__coppy-list a:hover {
  opacity: 0.7;
}
@media only screen and (max-width: 1500px) {
  .top-banner {
    background-position: -247px 0;
  }
}
@media only screen and (max-width: 1180px) {
  .wedding-item__left {
    padding: 20px 4px 20px 4px;
    width: 100%;
    float: none;
  }
  .wedding-item__right {
    margin: 0 auto;
    float: none;
  }
  .wedding-item__slider {
    margin: 0;
  }
  .wedding-item__slider img {
    height: auto;
  }
}
@media only screen and (max-width: 1170px) {
  .header__nav-list {
    display: none;
    margin-top: 10px;
    border-top: 2px solid #1c285b;
    border-bottom: 2px solid #1c285b;
  }
  .header__nav-list li {
    display: block;
    text-align: center;
    margin: 0;
    padding: 0;
  }
  .header__nav-list li:hover .header__dropdown {
    left: 16%;
  }
  .header__nav-list li:after {
    content: '';
    width: 0;
    height: 4px;
    transition: width 0.3s linear;
  }
  .header__nav-list li:hover:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    background-color: #1c285b;
    height: 4px;
    width: 100%;
  }
  .header__nav-list li:hover:after {
    content: '';
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: #1c285b;
    height: 4px;
    width: 100%;
  }
  .header__dropdown {
    width: 166px;
    padding: 12px 0 16.4px;
    top: 34px;
    z-index: 1;
  }
  .header__dropdown .dropdown__img {
    border-left: none;
  }
  .header__dropdown:before {
    content: none;
  }
  .header__dropdown li {
    margin: 0 0 10px;
  }
  .header__dropdown li:hover .dropdown__img {
    top: 100%;
    left: 0;
  }
  .header__gamburger {
    display: block;
  }
  .dropdown__img {
    border-left: 1px solid #fff;
    position: absolute;
    left: -9999px;
    top: 0;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.37);
  }
  .dropdown__img img {
    display: block;
  }
  .other-wedding {
    padding-bottom: 0;
  }
  .other-wedding__item {
    margin-bottom: 20px;
  }
}
@media only screen and (max-width: 1150px) {
  .footer__bottom {
    padding-bottom: 0;
  }
  .footer__bottom h2 {
    margin-bottom: 20px;
  }
  .footer__bottom-item {
    text-align: center;
    display: block;
    vertical-align: top;
    width: 100%;
    margin: 0 0 36px 0;
  }
  .footer__bottom-item--middle {
    padding-left: 0;
  }
  .footer__bottom-item--last {
    padding-left: 0;
  }
  .footer__bottom-list {
    text-align: left;
  }
  .footer__bottom-social {
    padding-left: 0;
    display: block;
    width: 255px;
    margin: 0 auto;
  }
}
@media only screen and (max-width: 790px) {
  .header__nav-list li:hover .header__dropdown {
    left: 0;
  }
  .header__dropdown {
    padding: 5px 0;
  }
  .header__dropdown li {
    margin-bottom: 10px;
  }
}
@media only screen and (max-width: 790px) {
  .other-wedding__img img {
    width: 100%;
  }
  .other-wedding__hover {
    padding: 19px 27px 10px 23px;
  }
  .other-wedding__item {
    width: 100%;
    display: block;
    margin: 0 auto 50px;
  }
  .other-wedding__item:last-child {
    margin: 64px auto;
  }
  .header__top {
    margin-bottom: 0;
  }
  .header__logo {
    position: static;
    float: none;
    display: block;
    width: 100%;
    margin: 0 auto;
  }
  .header__logo a {
    display: block;
  }
  .header__logo img {
    display: block;
    position: static;
    margin: 0 auto 30px;
  }
  .header__contacts {
    float: none;
    display: block;
    margin: 0 auto;
    text-align: center;
  }
}
@media only screen and (max-width: 520px) {
  .footer__coppy-right {
    display: block;
    margin: 0 auto;
    text-align: center;
    float: none;
  }
  .footer__coppy-left {
    margin: 0 auto;
    text-align: center;
    display: block;
  }
  .wedding-item__right {
    width: 100%;
  }
}
@media only screen and (max-width: 480px) {
  .header__contacts-btn {
    display: block;
    margin: 14px auto 0;
  }
  .header__contacts-phone {
    margin-right: 0;
  }
}
